.navbar {
  background: #F5F5F5;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 100px;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 50px;
}

.container {
  z-index: 1;
  width: 100%;
  max-width: 860px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 50px;
  padding-left: 50px;
  
}

.navBar button {
    
  right: 0px;
    top: 40px;
    cursor: pointer;
  }

.navbar-logo {
  color: #E40001;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.navbar-icon {
  margin-right: 0px;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  text-align: center;
  justify-content: end;
}

.nav-item {
  height: 87px;
  width: 100%;
  border-bottom: 2px solid transparent;
  font-weight: bold;
}

.nav-item:hover {
  border-bottom: 2px solid #f00946;
  color: #f00946;
  font-weight: bold;
}

.nav-links {
  color: #E40001;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  font-weight: bold;
}

.fa-bars {
  color: #fff;
}

.menu-icon {
  display: none;
  right: 0px;
}

@media screen and (max-width: 1300px) {
  .NavbarItems {
    position: relative;
  }



  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 92vh;
    position: absolute;
    top: 70px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    bottom: 70px;
    background: #F5F5F5;
  }

  .nav-menu.active {
    background: #F5F5F5;
    left: 0;
    opacity: 1;
    transition: all 0.6s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
    color: #f00946;
    font-weight: bold;
  }

  .nav-links:hover {
    color: #f00946;
    transform: scale(1.2);
    transition: all 0.3s ease;
    padding:  0.1px;
   
    font-weight: bold;
    
  }


  
  .nav-links-postpaid {
    text-align: left;
    padding: 1px;
    width: 90%;
    display: table;
  }

  .nav-item:hover {
    border: none;
    padding: 0.2px;
    transform: scale(1.2);
    transition: all 0.4s ease;
    color: #f00946;
    font-weight: bold;
    
  }

  .nav-item {
    width: 100%;
    padding: 1px;
    font-weight: bold;
    
  }

  .nav-item-postpaid {
    text-align: left;
    padding: 5px;
    width: 90%;
    display: table;
    border: none;
    padding: 40px;
    transform: scale(1.0);
  }
  .navbar-logo {
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: -10px;
    right: 0px;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #E40001;
  }

  .fa-times {
    color: #fff;
    font-size: 100px;
  }

  .nav-btn {
    display: flex;
    justify-content: right;
    align-items: right;
    width: 100%;
    height: 120px;
  }
  a.ex1:hover, a.ex1:active {
    color: red;
  }
  
  a.ex2:hover, a.ex2:active {
    font-size: 150%;
  }
 
  @media screen and (max-width: 767px) {
    .hidden-mobile {
      display: none;
    }
 
}
}